<template>
  <v-dialog
    v-model="Items.dialogOpen"
    persistent
    max-width="600px">

    <v-btn
      slot="activator"
      small
      color="deep-orange darken-1">{{ $ln('新增子帳號') }}</v-btn>

    <v-card>
      <v-card-title>
        <span class="headline">{{ $ln('新增子帳號') }}</span>
      </v-card-title>

      <v-card-text>
        <v-container grid-list-md>
          <v-form
            ref="form"
            v-model="Items.valid">
            <v-layout wrap>
              <v-flex
                xs12
                sm6
                md6>
                <v-select
                  v-model="Items.account.selected"
                  :items="Items.account.options"
                  :no-data-text="$ln('無可用帳號')"
                  :label="$ln('帳號')"
                  required
                />
              </v-flex>

              <v-flex
                xs12
                sm6
                md6>
                <v-text-field
                  v-model="Items.nameItems.name"
                  :rules="Items.nameItems.nameRules"
                  :label="$ln('名稱')"/>
              </v-flex>

              <v-flex
                xs12
                sm6
                md6>
                <v-text-field
                  v-model="Items.passwordItems.password"
                  :rules="Items.passwordItems.passwordRules"
                  :append-icon="Items.passwordItems.passwordIsVisible ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="Items.passwordItems.passwordIsVisible ? 'text' : 'password'"
                  :label="$ln('密碼')"
                  :hint="$ln('長度需為 4 位以上，並且是字母和數字的組合')"
                  required
                  @click:append="Items.passwordItems.passwordIsVisible = !Items.passwordItems.passwordIsVisible"
                />
              </v-flex>

              <v-flex
                xs12
                sm6
                md6>
                <v-text-field
                  v-model="Items.passwordItems.passwordConfirmation"
                  :rules="Items.passwordItems.passwordRules"
                  :append-icon="Items.passwordItems.passwordConfirmationIsVisible ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="Items.passwordItems.passwordConfirmationIsVisible ? 'text' : 'password'"
                  :label="$ln('確認密碼')"
                  :hint="$ln('長度需為 4 位以上，並且是字母和數字的組合')"
                  required
                  @click:append="Items.passwordItems.passwordConfirmationIsVisible = !Items.passwordItems.passwordConfirmationIsVisible"
                />
              </v-flex>

              <v-flex
                xs12
                sm6
                md4>
                <div class="v-input__slot">
                  <div class="v-text-field__slot">
                    <label
                      aria-hidden="true"
                      class="v-label theme--light"
                      style="left: 0px; right: auto; position: absolute;">{{ $ln('是否允許登入') }}</label>
                    <v-radio-group
                      v-model="Items.whetherAllowLogin.selected"
                      row>
                      <v-radio
                        class="v-input-checkbox"
                        v-for="(item, i) in Items.whetherAllowLogin.options"
                        :key="i"
                        :label="item.text"
                        :value="item.value"/>
                    </v-radio-group>
                  </div>
                </div>
              </v-flex>

              <v-flex
                v-show="Items.openSuperLottery.show"
                xs12
                sm6
                md4>
                <div class="v-input__slot">
                  <div class="v-text-field__slot">
                    <label
                      aria-hidden="true"
                      class="v-label"
                      style="left: 0px; right: auto; position: absolute;">{{ $ln('開放Super彩球') }}</label>
                    <v-radio-group
                      v-model="Items.openSuperLottery.selected"
                      row>
                      <v-radio
                        class="v-input-checkbox"
                        v-for="(item, i) in Items.openSuperLottery.options"
                        :key="i"
                        :label="item.text"
                        :value="item.value"/>
                    </v-radio-group>
                  </div>
                </div>
              </v-flex>

              <v-flex
                xs12
                sm6
                md4>
                <v-select
                  v-model="Items.identity.selected"
                  :items="Items.identity.options"
                  :label="$ln('身份')"
                  required>
                  <template slot='selection' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
                  <template slot='item' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
                </v-select>
              </v-flex>

              <v-flex xs12>
                <div style="margin-top: -30px;">
                  <v-textarea
                    v-model="Items.remark.contents"
                    :label="$ln('備註')"/>
                </div>
              </v-flex>
            </v-layout>
          </v-form>
        </v-container>
      </v-card-text>

      <v-card-actions>
        <v-spacer/>
        <v-btn
          color="blue darken-1"
          flat
          @click="Items.dialogOpen = false">{{ $ln('取消') }}</v-btn>
        <v-btn
          v-if="Items.valid"
          color="blue darken-1"
          flat
          @click="add()">{{ $ln('新增') }}</v-btn>
        <v-btn
          v-else
          disabled
          color="blue darken-1"
          flat>{{ $ln('新增') }}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { common } from '@/utils'

export default {
  name: 'AppendSubAccount',
  data () {
    return {
      // 新增子帳號資料
      Items: {
        dialogOpen: false,
        valid: false,
        // 帳號
        account: {
          selected: '',
          options: []
        },
        // 名稱資料
        nameItems: {
          // 名稱
          name: '',
          nameRules: [] // *initLanguage*
          //['不能為空格', '不能為特殊符號或空格，除了「括號()」、「底線_」和「-」']
        },
        // 密碼資料
        passwordItems: {
          // 密碼
          password: '',
          passwordIsVisible: false,
          // 確認密碼
          passwordConfirmation: '',
          passwordConfirmationIsVisible: false,
          passwordRules: [] // *initLanguage*
          //['不能為空格', '長度需為 4 位以上', '需為英文或數字', '需為英文加數字組合', '需為英文加數字組合']
        },
        // 是否允許登入(0：允許登入 1：不允許登入)
        whetherAllowLogin: {
          selected: 0,
          options: [] // *initLanguage*
          //['是', '否']
        },
        // 開放Super彩球(0：開放 1：不開放)
        openSuperLottery: {
          show: true,
          selected: 1,
          options: [] // *initLanguage*
          //['是', '否']
        },
        // 身份
        identity: {
          selected: '查詢員',
          options: [
            {
              text: '查詢員',
              value: '查詢員'
            }
          ]
        },
        // 備註
        remark: {
          contents: ''
        }
      }
    }
  },
  computed: {
    ...mapGetters({
      getGenerateHleadAccountState: 'account-management/getGenerateHleadAccountState',
      getDashboardInfoState: 'account-management/getDashboardInfoState'
    })
  },
  watch: {
    '$store.state.lang' (value) {
    this.initLanguage()
    },
    getGenerateHleadAccountState (payload) {
      this.Items.account.selected = payload.result.account[0]
      this.Items.account.options = payload.result.account
    },
    'Items.dialogOpen' (dialogOpen) {
      // 關閉新增帳號彈窗
      if (!dialogOpen) {
        // 清除欄位資料
        this.clearField()
        // 關閉彈窗 reset 驗證功能
        this.$refs.form.resetValidation()
      }
    }
    // getDashboardInfoState () {
    //   this.updateDashboardInfo()
    // }
  },
  mounted () {
    this.initLanguage()
    // 取得自動生成帳號編號(子帳號)
    this.loadGenerateAccount()

    this.updateDashboardInfo()
  },
  methods: {
    ...mapActions({
      postSubAccount: 'api/postSubAccount',
      getGenerateAccount: 'api/getGenerateAccount'
    }),
    add () {
      let addData = {
        l_no: this.Items.account.selected,
        l_name: this.Items.nameItems.name,
        l_pwd: this.Items.passwordItems.password,
        l_pwd_confirmation: this.Items.passwordItems.passwordConfirmation,
        is_close: this.Items.whetherAllowLogin.selected,
        remark: this.Items.remark.contents
      }
      // 開放Super彩球
      if (this.Items.openSuperLottery.show) addData.open_super = this.Items.openSuperLottery.selected
      try {
        this.postSubAccount(addData)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(`${this.$ln('新增失敗')}`)
            } else if (res.status === 200) { // success
              this.$toastr.success(`${this.$ln('新增成功')}`, '', {timeOut: 2000, closeButton: false})
              // 清除欄位資料
              this.clearField()
              // 新增成功狀態
              this.$emit('status', true)
              // 刷新自動生成帳號編號(子帳號)
              this.loadGenerateAccount()
              // 關閉彈窗
              this.Items.dialogOpen = false
            }
          })
          .catch(err => {
            this.$toastr.error(`${this.$ln('新增失敗')}`)
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(`${this.$ln('站台資料錯誤，請聯絡客服')}`)
        console.error(error)
      }
    },
    loadGenerateAccount () {
      try {
        this.getGenerateAccount({ type: 'hlead' })
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(`${this.$ln('取得可新增子帳號失敗')}`)
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得可新增子帳號成功`)
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(`${this.$ln('站台資料錯誤，請聯絡客服')}`)
        console.error(error)
      }
    },
    clearField () {
      this.Items.nameItems.name = ''
      this.Items.passwordItems.password = ''
      this.Items.passwordItems.passwordConfirmation = ''
      this.Items.whetherAllowLogin.selected = 0
      this.Items.openSuperLottery.selected = 0
      this.Items.identity.selected = '查詢員'
      this.Items.remark.contents = ''
    },
    // 更新大後台設置資訊
    updateDashboardInfo () {
      let dashboardInfo = JSON.parse(sessionStorage.getItem('super.manage.dashboard.info'))
      if (dashboardInfo && dashboardInfo.hasOwnProperty('noOpenGame')) {
        // 不開放遊戲設定
        for (let i in dashboardInfo.noOpenGame) {
          this.closeGameType(dashboardInfo.noOpenGame[i])
        }
      }
    },
    // 不開放遊戲
    closeGameType (type) {
      switch (type) {
        // 關閉Super彩球
        case 's17':
          this.Items.openSuperLottery.show = false
          break
      }
    },
    // 初始語系
    initLanguage () {
      this.Items.nameItems.nameRules = [
        v => !!v || this.$ln('不能為空格'),
        v => ((/^[\u4e00-\u9fa5_A-Za-z0-9\(\)\_\-]+$/.test(v)) || v === '') || this.$ln('不能為特殊符號或空格，除了「括號()」、「底線_」和「-」'),
        v => v.length <= 20 || this.$ln('不能超過 20 個字元')
      ]
      this.Items.passwordItems.passwordRules = [
        v => !!v || this.$ln('不能為空格'),
        v => (v.length === 0 || v.length >= 4) || this.$ln('長度需為 4 位以上'),
        v => ((/^[A-Za-z0-9]+$/.test(v)) || v === '') || this.$ln('需為英文或數字'),
        v => (!(/^[0-9]+$/.test(v)) || v === '') || this.$ln('需為英文加數字組合'),
        v => (!(/^[A-Za-z]+$/.test(v)) || v === '') || this.$ln('需為英文加數字組合')
      ]
      this.Items.whetherAllowLogin.options = [
        {text: this.$ln('是'),value: 0},
        {text: this.$ln('否'),value: 1}
      ]
      this.Items.openSuperLottery.options = [
        {text: this.$ln('是'),value: 0},
        {text: this.$ln('否'),value: 1}
      ]
    }
  }
}
</script>

<style lang="css">
</style>
